@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;700&display=swap);
nav .nav-wrapper {
  display: flex;
}

button.sidenav-trigger,
button.sidenav-trigger:active,
button.sidenav-trigger:focus {
  display: block;
  outline: none;
  border: none;
  background: none;
  cursor: pointer;
}

@media (min-width: 992px) {
  button.sidenav-trigger {
    display: none;
  }
}

.progress {
    position: fixed !important;
}
table button {
  margin: 0.2rem;
}
.add-btn {
  margin: 25px 0;
}

.add-btn span {
  margin-right: 10px;
}

.row {
    padding: 25px 0 ;
}
.cart {
  display: flex;
  justify-content: right;
}

.cart button {
  margin-top: 25px;
}

.cart button i {
  margin: 0 5px;
}

.cart button .badge {
  font-weight: bold !important;
}

.dropdown-content {
  padding: 20px 10px;
  margin: 20px 0;
  top: 130px !important;
}

.dropdown-content li {
  font-size: 0.7em;
  cursor: default !important;
}

.dropdown-content .collection-item div {
  display: flex;
  justify-content: space-between;
}

.dropdown-content li:hover {
  background-color: #fff !important;
}


.collection-item div {
    display: flex;
    justify-content: space-between;
}
/* base styles */

* {
  margin: 0;
  padding: 0;
}

html,
body,
#root {
  height: 100vh;
}
body {
  font-family: "Nunito", sans-serif;
  font-size: 2em;
  background: #fafafa;
  color: #333;
}

.no-items {
  margin: 25px 0;
  font-size: 0.7em;
}

.row {
  margin-top: 25px;
}

.cart button,
.card button {
  display: flex;
  align-items: center;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

